.summit {
  color: #333;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  .MuiTypography-h2 {
    font-size: 24px !important;
  }
  .term-content {
    // min-height: 200px;
    height: 30vh;
    overflow: auto;
    border: 1px solid #ddd;
    padding: 10px;
    color: #555;
    line-height: 1.8em;
  }
  .image-content {
    // min-height: 200px;
    // height: 50vh;
    // overflow: auto;
    border: 1px solid #ddd;
    padding: 5px;
    margin: 0 auto;
    // color: #555;
    // line-height: 1.8em;
  }
  .error {
    color: #f00 !important;
  }
  .tips {
    height: 20px;
    padding-top: 10px;
  }
  .bt-1-c {
    border-top: 1px solid #ccc;
    padding: 10px 0;
    text-align: center;
  }
  &-header {
    height: 50px;
    color: #fff;
    // background-color: #232f3e;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .logo {
      vertical-align: middle;
      padding-left: 40px;
      padding-top: 0px;
      display: flex;
      align-items: center;
      // vertical-align: middle;
    }
    .title {
      vertical-align: middle;
      font-weight: bold;
      padding-left: 20px;
    }
    .admin {
      padding-top: 0px;
      padding-right: 50px;
      a {
        color: #fff;
        text-decoration: none;
        margin-left: 10px;
      }
    }
  }
  &-content {
    flex: 1;
    position: relative;
    .summit-slogan {
      position: absolute;
      right: 50px;
      top: 20px;
    }
    .container {
      position: absolute;
      padding: 20px 50px;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      overflow-y: auto;
      .bread-crumb {
        display: flex;
        align-content: center;
        .item {
          color: #0073bb;
          // display: inline-block;
          margin-right: 10px;
          &.icon {
            margin-top: 2px;
          }
          &.text {
            color: #8caffa;
          }
          a {
            color: #0073bb;
            text-decoration: none;
          }
        }
      }
      .step-title {
        margin-top: 10px;
        font-size: 36px;
        padding: 5px 0;
        color: #fff;
      }
      .step-desc {
        color: #fff;
        font-size: 14px;
        line-height: 1.8em;
      }
      .demo-area {
        position: relative;
        z-index: 1;
        min-width: 900px;
        // min-height: 500px;
        background-color: #fff;
        margin: 20px auto 0;
        .preview-image {
          position: relative;
          display: flex;
          margin: 0 auto;
          align-items: center;
          justify-content: center;
          vertical-align: middle;
          text-align: center;
        }
        .home-content-title {
          font-size: 24px;
          font-weight: bold;
          text-align: center;
          line-height: 70px;
          height: 70px;
          background-color: #f4f4f4;
        }
        .home-content-options {
          padding: 40px 10%;
          justify-content: center;
          .item {
            width: 400px;
            height: 258px;
            margin: 0 30px;
            border-radius: 27px;
            padding-top: 100px;
            cursor: pointer;
            &.photo {
              box-shadow: 0px 6px 8px #2028c6;
              background: rgb(108, 57, 173);
              background: radial-gradient(circle, rgba(108, 57, 173, 1) 0%, rgba(25, 11, 93, 1) 100%);
            }
            &.video {
              box-shadow: 0px 6px 8px #66d4ff;
              background: rgb(58, 132, 200);
              background: radial-gradient(circle, rgba(58, 132, 200, 1) 0%, rgba(43, 65, 127, 1) 100%);
            }
            .item-title {
              text-align: center;
              font-size: 38px;
              font-weight: bold;
              color: rgba($color: #ffffff, $alpha: 0.9);
            }
            .item-desc {
              padding: 50px 0 0 50px;
              line-height: 1.5em;
              color: #fff;
            }
          }
        }
        .home-content-desc {
          color: #acacac;
          background-color: #f4f4f4;
          font-size: 18px;
          line-height: 1.6em;
          padding: 30px 150px;
        }
        .demo-title {
          font-size: 24px;
          // font-weight: bold;
          padding: 20px 0 20px 20px;
        }
        .step-guide {
          display: flex;
          justify-content: space-between;
          background-color: #f3f3f3;
          align-items: center;
          padding: 20px 15%;
          .step-name {
            padding: 10px 0 0 0;
          }
          // .angle-right {
          //   // padding-bottom: 20%;
          //   // display: flex;
          //   // align-content: center;
          //   // height: 100%;
          //   // vertical-align: middle;
          //   // align-self: center;
          // }
        }
        .step-image {
          display: flex;
          justify-content: space-between;
          // align-items: flex-end;
          padding: 10px 0%;
          // flex-wrap: wrap;
          &.info {
            padding-left: 15%;
            padding-right: 15%;
          }
          .fix-count-down {
            position: absolute;
            z-index: 99;
            font-size: 72px;
            font-weight: bold;
            color: rgba($color: #ffffff, $alpha: 0.6);
            top: 100px;
            width: 100%;
            text-align: center;
          }
          .choose-bg {
            padding-left: 5%;
            padding-right: 5%;
          }
          .image-item {
            width: 25%;
            padding: 15px 15px;
            opacity: 0.7;
            img {
              cursor: pointer;
              border: 4px solid transparent;
              padding: 3px;
            }
            &.active {
              opacity: 1;
              img {
                border-radius: 15px;
                border: 4px solid #271e84;
                padding: 3px;
              }
            }
            &-video {
              width: 75%;
              // padding: 10px 40px;
              opacity: 0.7;
              img {
                cursor: pointer;
                border: 4px solid transparent;
                padding: 3px;
                margin: 5px 40px;
              }
              &.active {
                opacity: 1;
                img {
                  border-radius: 15px;
                  border: 4px solid #271e84;
                  padding: 3px;
                }
              }
            }
            &-print {
              // width: 20%;
              flex: 1;
              padding: 10px 20px;
            }
          }
        }
        .done {
          text-align: center;
          padding-top: 50px;
          font-size: 24px;
          .email {
            margin-top: 10px;
            border: 1px solid #ccc;
            padding: 10px;
            font-size: 28px;
          }
        }
        .start-button {
          padding: 10px 0 20px 0;
        }
        .print-email-label {
          padding: 20px 10px 0 40px;
          font-size: 24px;
        }
        .print-email {
          padding: 20px 5%;
          input {
            font-size: 24px;
            border: 1px solid #ccc;
            padding: 10px 20px;
            width: 100%;
          }
        }
        .setting-item {
          padding: 20px 40px;
          .label {
            // padding: 5px;
            padding: 5px 5px;
            width: 140px;
            font-size: 16px;
          }
          .input {
            padding: 0 5px;
            input {
              font-size: 16px;
              padding: 5px;
              border: 1px solid #aaa;
            }
          }
        }
      }
    }
    .place-bg {
      z-index: 0;
      .item {
        width: 280px;
        // border: 1px solid #ccc;
        // z-index: 0;
        position: absolute;
        bottom: 20px;
        text-align: center;
        z-index: -1;
        &.one {
          left: 10px;
        }
        &.two {
          left: 50%;
          margin-left: -140px;
        }
        &.three {
          right: 10px;
        }
      }
    }
  }
  &-footer {
    height: 30px;
    background-color: #232f3e;
  }
  .select-img {
    img.active {
      border: 1px solid #f00;
      padding: 3px;
    }
  }
}

@media (max-width: 768px) {
  .logo {
    padding-left: 10px !important;
  }
}
